@import "variables";

/******* Base styles *******/
html {
    height: 100%;
}

body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $font-family;
    font-size: 14px;
    line-height: 1.5;
}

* {
    margin: 0;
    padding: 0;
}

a {
    outline: none;
}

.h-100 {
    height: 100% !important;
}

.w-100 {
    width: 100% !important;
}

.mw-100 {
    max-width: 100%;
}

.fw-300 {
    font-weight: 300;
}

.fw-400 {
    font-weight: 400;
}

.fw-500 {
    font-weight: 500;
}

.lh {
    line-height: 27px;
}

.theme-container {
    max-width: $theme-max-width;
    margin: 0 auto;
    width: 100%;
}

.light-block.mat-card {
    background: rgba(0, 0, 0, 0.03) !important;
    box-shadow: none !important;
}

.transition {
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    transition: 0.3s;
}

.text-truncate {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden !important;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.divider {
    border-top: 1px solid rgba(204, 204, 204, 0.6);
}


// ====== Top Search Box Css UT Start ========
