}

.px-2 {
    padding: 0 20px !important;
}

.px-3 {
    padding: 0 30px !important;
}

.px-4 {
    padding: 0 40px !important;
}

.px-5 {
    padding: 0 50px !important;
}

.pl-3 {
    padding-left: 30px;
}

.m-0 {
    margin: 0 !important;
}

.mt-1 {
    margin-top: 10px !important;
}

.mt-2 {
    margin-top: 20px !important;
}

.mt-3 {
    margin-top: 30px !important;
}

.mr-1 {
    margin-right: 10px !important;
}

.d-block {
