.social-icon {
    width: 48px;
    height: 48px;
    cursor: pointer;
    fill: #8f8f8f;
    outline: none;
}

.app {
    &.main-toolbar-fixed {
        #menu-toolbar {
            position: fixed;
            top: 0px;
            width: 100%;
            z-index: 9;
            box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2),
                0 2px 2px 0 rgba(0, 0, 0, .14),
                0 1px 5px 0 rgba(0, 0, 0, .12);
        }

        .main {
            padding-top: 64px;
        }
    }
}


/******* Toolbar *******/
.top-toolbar.mat-toolbar-row {
    height: 32px;
    font-size: 13px;

    .sidenav-toggle {
        height: 31px;
        line-height: 31px;
        min-width: 24px;
        padding: 0;
        border-radius: 0;
    }

    .top-menu {
        margin-right: -16px;
        text-transform: uppercase;

        a {
            text-decoration: none;
            font-weight: 400;
            font-size: 13px;
            line-height: 31px;
            padding: 0 10px;
            min-width: 70px;
            border-radius: 0;
        }

        a.mobile-menu-icon {
            min-width: 36px;
        }

        .mat-icon.more {
            height: 28px;
        }
    }
}

.flag-menu-title {
    vertical-align: top !important;
    margin-left: 3px;
}

.top-menu-dropdown .mat-menu-item img {
    vertical-align: middle;
    margin-right: 8px;
}

.cart-dropdown.mat-menu-panel {
    padding: 6px 14px;
    font-size: 13px;

    .mat-list {
        padding: 0;

        .mat-list-item {
            .mat-list-item-content {
                padding: 0;

                img {
                    width: 70px;
                    margin-left: 10px;
                }
            }

            .mat-list-text>* {
                font-size: 14px;
                font-weight: 500;
            }

            .mat-line:nth-child(n+2) {
                font-size: 13px;
                font-weight: 400;
            }
        }
    }

    .cart-dropdown-footer {
        font-size: 14px;
    }
}

.app-menu-panel.mat-menu-panel {
    min-width: 60px;

    .mat-menu-content:not(:empty) {
        padding: 0;
    }

    .mat-menu-item {
        display: flex;
        align-items: center;
        font-size: 12px;
        text-transform: uppercase;

        .mat-badge-small.mat-badge-above .mat-badge-content {
            top: 8px;
        }
    }

    &.lang {
        min-width: 41px;

        .mat-menu-item {
            line-height: 36px;
            height: 36px;
            padding: 0;

            img {
                display: block;
                margin: 0 auto;
            }
        }
    }
}

.logo-toolbar.mat-toolbar-row {
    height: 104px;
}

.logo {
    font-size: 28px;
    font-weight: 900;
    text-transform: uppercase;
    text-decoration: none;
    color: inherit;
    display: inline-block;

    img {
        max-width: 75px;
        width: 100%;
        height: auto;
        padding-top: 10px;
    }
}

.app.green .mat-toolbar-row,
.app.green .mat-toolbar-single-row {
    height: inherit !important;
}

.leftCatBtn {
    padding: 0;
}

span.topPhoneNumber {
    font-size: 15px;
}

.search-form {
    border-radius: 22px;
    overflow: hidden;

    .categories {
        height: 40px;
        width: 180px;
        border-radius: 0;
    }

    input[type=text] {
        border: 0;
        outline: none;
        padding: 0 34px 0 12px;
        border-left: 1px solid #ccc;
    }

    .search-btn {
        margin-left: -20px;
        background: #fff !important;
        box-shadow: none !important;
    }
}

.search-dropdown.mat-menu-panel {
    border-radius: 26px;
    max-width: 100%;
    min-height: auto;

    .mat-menu-content {
        padding: 0;
    }
}

.top-cart {
    span {
        font-size: 13px;
        font-weight: 400;
        line-height: 1.2;
    }
}


/******* Top navbar (menu) *******/
.top-navbar.mat-toolbar {
    height: 48px;
    padding: 0;

    a {
        min-width: 70px;
        border-radius: 0;
        text-transform: uppercase;
        padding: 6px 16px;
    }
}

.cdk-overlay-pane.mega-menu-pane {
    left: 0 !important;
    width: 100%;

    .mat-menu-panel {
        width: 100%;
        max-width: none;
        border-radius: 0;

        .mat-menu-content {
            padding: 0;
        }
    }

    .mega-menu-widget {
        position: relative;

        button {
            position: absolute;
            top: 10px;
            left: 10px;
            font-weight: 400;
        }

        .mat-chip-list {
            position: absolute;
            bottom: 10px;
            left: 10px;
        }
    }
}


/******* Carousel *******/
button.swipe-arrow {
    position: absolute !important;
    top: 50%;
    margin-top: -22px;
    z-index: 10;
    background-image: none;
}

.swiper-pagination.white .swiper-pagination-bullet {
    background: #fff;
}

.swiper-pagination-bullet-active {
    width: 12px;
    height: 12px;
    vertical-align: -2px;
}

.swiper-button-prev:after,
.swiper-container-rtl .swiper-button-next:after,
.swiper-button-next:after,
.swiper-container-rtl .swiper-button-prev:after {
    content: none;
}

/******* Product *******/
.product-item {
    position: relative;

    .mat-chip-list {
        position: absolute;
        top: 10px;
        right: 10px;

        &.list-view {
            left: 10px;
        }
    }

    .image-link {
        display: block;
    }

    img {
        max-width: 100%;
        margin-bottom: 12px;
    }

    .category {
        margin-bottom: 4px;
    }

    .title {
        display: block;
        color: inherit;
        text-decoration: none;
        font-weight: 500;
        font-size: 18px;
    }

    .prices {
        margin: 12px 0;

        .old-price {
            text-decoration: line-through;
            height: 15px;
        }

        .new-price {
            font-size: 16px;
            font-weight: 500;
        }
    }

    .icons {
        margin-top: 18px;
    }
}

.old-price {
    text-decoration: line-through;
}

.mat-horizontal-content-container {
    padding: 24px 24px 24px 24px !important;
}

/******* Back to top *******/
.back-to-top {
    position: fixed;
    width: 40px;
    height: 40px;
    cursor: pointer;
    z-index: 999999;
    right: 20px;
    bottom: 20px;
    opacity: .5;
    color: #fff;
    background-color: rgba(0, 0, 0, .75);
    border-radius: 4px;

    &:hover {
        opacity: 0.9;
    }
}



/******* Responsive *******/
// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575px) {
    .top-toolbar.mat-toolbar-row {
        padding: 0;

        .top-menu {
            margin-right: 0;

            a {
                min-width: 56px;
            }
        }

        .sidenav-toggle {
            min-width: 52px;
        }
    }

    .search-form input[type=text] {
        width: 160px;
    }

    .search-form .categories {
        width: 140px;
    }

}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767px) {
    .top-toolbar.mat-toolbar-row {
        padding: 0;

        .top-menu {
            margin-right: 0;
        }

        .sidenav-toggle {
            min-width: 52px;
        }
    }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991px) {
    .top-toolbar.mat-toolbar-row {
        padding: 0;

        .top-menu {
            margin-right: 0;
        }

        .sidenav-toggle {
            min-width: 52px;
        }
    }

}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199px) {}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {}


::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-button {
    width: 0px;
    height: 0px;
}

::-webkit-scrollbar-thumb {
    background: #e1e1e1;
    border: 0px none #ffffff;
    border-radius: 0px;

    &:hover {
        background: #cccccc;
    }

    &:active {
        background: #888888;
    }
}

::-webkit-scrollbar-track {
    background: #666666;
    border: 0px none #ffffff;
    border-radius: 0px;

    &:hover {
        background: #666666;
    }

    &:active {
        background: #333333;
    }
}

::-webkit-scrollbar-corner {
    background: transparent;
}



/* perfect-scrollbar */
.ps {
    -ms-touch-action: auto;
    touch-action: auto;
    overflow: hidden !important;
    -ms-overflow-style: none;
    position: relative;
}

@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
    .ps {
        overflow: auto !important;
    }
}

.ps__rail-x {
    display: none;
    opacity: 0;
    transition: background-color .2s linear, opacity .2s linear;
    -webkit-transition: background-color .2s linear, opacity .2s linear;
    height: 7px;
    bottom: 0px;
    position: absolute;
}

.ps__rail-y {
    display: none;
    opacity: 0;
    transition: background-color .2s linear, opacity .2s linear;
    -webkit-transition: background-color .2s linear, opacity .2s linear;
    width: 7px;
    right: 0px;
    left: auto !important;
    position: absolute;
}

.ps--active-x>.ps__rail-x,
.ps--active-y>.ps__rail-y {
    display: block;
    background-color: transparent;
}

.ps:hover>.ps__rail-x,
.ps:hover>.ps__rail-y,
.ps--focus>.ps__rail-x,
.ps--focus>.ps__rail-y,
.ps--scrolling-x>.ps__rail-x,
.ps--scrolling-y>.ps__rail-y {
    opacity: 0.6;
}

.ps__rail-x:hover,
.ps__rail-y:hover,
.ps__rail-x:focus,
.ps__rail-y:focus {
    background-color: #eee;
    opacity: 0.9;
}

.ps__thumb-x {
    background-color: #aaa;
    border-radius: 6px;
    transition: background-color .2s linear, height .2s ease-in-out;
    -webkit-transition: background-color .2s linear, height .2s ease-in-out;
    height: 4px;
    bottom: 1px;
    position: absolute;
}

.ps__thumb-y {
    background-color: #aaa;
    border-radius: 6px;
    transition: background-color .2s linear, width .2s ease-in-out;
    -webkit-transition: background-color .2s linear, width .2s ease-in-out;
    width: 4px;
    right: 1px;
    position: absolute;
}

.ps__rail-x:hover>.ps__thumb-x,
.ps__rail-x:focus>.ps__thumb-x {
    background-color: #999;
    height: 7px;
}

.ps__rail-y:hover>.ps__thumb-y,
.ps__rail-y:focus>.ps__thumb-y {
    background-color: #999;
    width: 7px;
}

@supports (-ms-overflow-style: none) {
    .ps {
        overflow: auto !important;
    }
}

@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
    .ps {
        overflow: auto !important;
    }
}


@media (min-width: 1380px) {
    .theme-container {
        max-width: 1400px;
    }
}

.sidenav .mat-drawer-inner-container {
    overflow-x: hidden;
}